<template>
    <div class="mode">
        <navigate title="模式切换" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-list>
                <mu-list-item class="border-1px-b">
                    <mu-list-item-title>切换模式</mu-list-item-title>
                    <mu-list-item-action>
                        <div style="margin-left: -75px;">
                            <m-switch :value="mode" :items="[THEME_LIGHT, THEME_NIGHT]"
                                      @change="toggle"/>
                        </div>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item @click.native="onSelectedTime" avatar>
                    <mu-list-item-content>
                        <mu-list-item-title>自定义时间</mu-list-item-title>
                        <mu-list-item-sub-title>
                            {{modeSn | formatDate('HH:mm')}} ~
                            {{modeEn | formatDate('HH:mm')}}
                        </mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-icon value="chevron_right"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>

        <!-- 时间选择器 -->
        <mu-bottom-sheet :open.sync="open">
            <div class="date-header border-1px-b">
                <mu-sub-header>请选择切换时间</mu-sub-header>
                <mu-icon value="close" @click="open = false"/>
            </div>
            <div class="date-wrapper">
                <div class="time-select border-1px-r">
                    <div class="time-select-item border-1px-b"
                         :class="{active: index === 0}"
                         @click="index = 0">开始时间
                    </div>
                    <div class="time-select-item"
                         :class="{active: index === 1}"
                         @click="index = 1">结束时间
                    </div>
                </div>
                <mu-time-picker format="24hr" :time="showTime" view-type="list"
                                @change="onTimeChange"
                                no-display/>
            </div>
        </mu-bottom-sheet>
        <!-- 时间选择器 -->
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import MSwitch from '../../../base/switch/switch'
    import {MapMixins} from "../../../common/js/mixins"
    import {appMixins} from "../../../common/mixins"
    import {formatDate} from "../../../common/utils/time"

    export default {
        name: "mode",
        mixins: [appMixins, MapMixins],
        data() {
            return {
                open: false,
                index: 0
            }
        },
        computed: {
            showTime() {
                return this.index === 0 ? new Date(this.modeSn) : new Date(this.modeEn)
            }
        },
        methods: {
            /***
             * 切换模式
             * @param mode  =>  light: 白天 / dark: 夜晚
             */
            toggle(mode) {
                this.setMode(mode)

                this.setModeTiming()
            },
            /***
             * 点击选择时间
             */
            onSelectedTime() {
                this.open = true
            },
            onTimeChange(time, mode, finished) {
                if (this.index === 0) {
                    this.setModeSn(new Date(time).getTime())
                }

                if (this.index === 1) {
                    this.setModeEn(new Date(time).getTime())
                }

                this.setModeTiming()
            }
        },
        filters: {
            formatDate: formatDate
        },
        components: {
            Navigate,
            MSwitch
        }
    }
</script>

<style lang="stylus" scoped>
    .mode
        padding-top 46px

        .scroll-wrapper
            width 100%
            height 100%

    .date-header
        display flex
        align-items center
        justify-content space-between

        .mu-icon
            margin-right 15px

    .date-wrapper
        display flex

        .time-select
            width 95px
            overflow-x hidden
            overflow-y auto

            .time-select-item
                width 100%
                height 50px
                line-height 50px
                text-align center

                &.active
                    color #2196f3
</style>



